/* ---------------------------------------------------------
   CSS RESET & NORMALIZATION
--------------------------------------------------------- */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;

  background-color: #fafafa;            /* neutral-50 */
  color: #171717;                        /* neutral-900 */

  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    "Helvetica Neue",
    Arial,
    "Noto Sans",
    "Apple Color Emoji",
    "Segoe UI Emoji",
    "Segoe UI Symbol",
    "Noto Color Emoji",
    sans-serif;

  font-size: 15px;
  line-height: 1.55;

  min-height: 100vh;
}


/* ---------------------------------------------------------
   TYPOGRAPHY SYSTEM
--------------------------------------------------------- */

/* Headings — restrained, not dramatic */
h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  letter-spacing: -0.01em;
  color: #111111;
}

h1 {
  font-size: 2.25rem;   /* ~36px */
  line-height: 1.25;
}

h2 {
  font-size: 1.75rem;   /* ~28px */
  line-height: 1.3;
}

h3 {
  font-size: 1.375rem;  /* ~22px */
}

h4 {
  font-size: 1.125rem;  /* ~18px */
}

p {
  margin: 0;
  color: #404040;       /* neutral-700 */
}

small {
  font-size: 0.8125rem;
  color: #525252;       /* neutral-600 */
}


/* ---------------------------------------------------------
   LINKS
--------------------------------------------------------- */
a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}


/* ---------------------------------------------------------
   FORM ELEMENTS
--------------------------------------------------------- */
input,
textarea,
select {
  font-family: inherit;
  font-size: 14px;
  color: #171717;

  border-radius: 6px;
  border: 1px solid #e5e5e5; /* neutral-200 */
  background-color: #ffffff;

  padding: 0.5rem 0.75rem;
}

input::-moz-placeholder, textarea::-moz-placeholder {
  color: #a3a3a3; /* neutral-400 */
}

input::placeholder,
textarea::placeholder {
  color: #a3a3a3; /* neutral-400 */
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: #000000;
  box-shadow: 0 0 0 1px #000000;
}

textarea {
  resize: vertical;
  line-height: 1.6;
}


/* ---------------------------------------------------------
   BUTTON BASELINE
--------------------------------------------------------- */
button {
  font-family: inherit;
  cursor: pointer;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


/* ---------------------------------------------------------
   CODE & MONOSPACE (VERY IMPORTANT FOR TRUST)
--------------------------------------------------------- */
code,
pre,
textarea.font-mono {
  font-family:
    ui-monospace,
    SFMono-Regular,
    Menlo,
    Monaco,
    Consolas,
    "Liberation Mono",
    "Courier New",
    monospace;
}

pre {
  background-color: #f5f5f5;
  border-radius: 6px;
  padding: 1rem;
  overflow-x: auto;
}

code {
  background-color: #f5f5f5;
  padding: 0.2rem 0.3rem;
  border-radius: 4px;
}


/* ---------------------------------------------------------
   BORDERS, PANELS & CARDS
--------------------------------------------------------- */
.card {
  background-color: #ffffff;
  border: 1px solid #e5e5e5;
  border-radius: 10px;
}

.panel {
  background-color: #ffffff;
  border-right: 1px solid #e5e5e5;
}


/* ---------------------------------------------------------
   TABLES (Claims, Reports, Risk Tables)
--------------------------------------------------------- */
table {
  border-collapse: collapse;
  width: 100%;
  font-size: 14px;
}

thead th {
  text-align: left;
  font-weight: 500;
  color: #525252;
  border-bottom: 1px solid #e5e5e5;
  padding: 0.5rem;
}

tbody td {
  padding: 0.5rem;
  border-bottom: 1px solid #f0f0f0;
  color: #262626;
}

tbody tr:hover {
  background-color: #fafafa;
}


/* ---------------------------------------------------------
   STATUS COLORS (STRICTLY LIMITED)
--------------------------------------------------------- */
.status-success {
  color: #166534; /* green-800 */
}

.status-warning {
  color: #92400e; /* amber-800 */
}

.status-danger {
  color: #991b1b; /* red-800 */
}

.badge {
  display: inline-flex;
  align-items: center;
  padding: 0.2rem 0.5rem;
  border-radius: 999px;
  font-size: 0.75rem;
  font-weight: 500;
}


/* ---------------------------------------------------------
   SCROLLBARS (SUBTLE, NOT FLASHY)
--------------------------------------------------------- */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.15);
  border-radius: 8px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}


/* ---------------------------------------------------------
   ANIMATION PHILOSOPHY
   (Minimal — trust tools should not dance)
--------------------------------------------------------- */
.fade-in {
  animation: fadeIn 120ms ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


/* ---------------------------------------------------------
   SELECTION
--------------------------------------------------------- */
::-moz-selection {
  background-color: #000000;
  color: #ffffff;
}
::selection {
  background-color: #000000;
  color: #ffffff;
}
.hover\:bg-blue-50:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(239 246 255 / var(--tw-bg-opacity));
}
.hover\:bg-gray-50:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(249 250 251 / var(--tw-bg-opacity));
}
.hover\:bg-gray-800:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(31 41 55 / var(--tw-bg-opacity));
}
.hover\:bg-neutral-50:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(250 250 250 / var(--tw-bg-opacity));
}
.hover\:bg-neutral-800:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(38 38 38 / var(--tw-bg-opacity));
}
.hover\:underline:hover {
  text-decoration-line: underline;
}
.disabled\:opacity-50:disabled {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

